import sao from './components/saosao.vue';
export default {
  components:{sao:sao},
  name: 'App',
  data(){
    return {
      loading:false,
      saoloding:false,
    }
  },
  //
  created() {
    if(this.getCookie('userCode') == null){
        this.$router.push({path:'/login'});
        return
      }
  },
  mounted(){
  	//busloading
    var _this = this
  	global.bus.$on("showload",function(val){
  		_this.loading = val
    });
    global.bus.$on("saoshowload",function(val){
      _this.saoloding = val
    });
  },
  methods:{

  }
}



