export default {
    name:'foot',
    data () {
        return {
            fontdata:["","","","",""]
        }
    },
    methods:{
        restor(){
            let setli=$(".foot ul li");
            let num=0;
            for(let i=0;i<5;i++){
                 num=i+1;
                $(setli[i]).children("span").eq(0).removeClass("TopImg"+num+"t").addClass("TopImg"+num);
                 $(setli[i]).children("span").eq(1).removeClass("SizeColor");
            }
        },
        //foot
        home(){
            this.restor();
            global.cliction=1;
            this.reload();
        },
        mes(){
            this.restor();
            global.cliction=5;
            this.reload();
        },
        state(){
            this.restor();
            global.cliction=2;
            this.reload();
        },
        person(){
            this.restor();
            global.cliction=4;
            this.reload();
        },
        //
        Homepage:function(e){
            this.$router.push({path:'/home'});
        },
        Message:function(e){
            this.$router.push({path:'/personalCenter'});
        },
        chart(e){
            this.$router.push({path:'/homemenu',query:{id:"000114",text:""}})
        },
        ME(e){
            this.$router.push({
                path:"/assetpage/",
            query: { name:"", id:"22",url:"/asm/Asset/Search?tabid=22&assetstatus=manage&operatetype=1&APIUrl=assetpage"}

            });       
            
        },
        Adminste(e){
            this.restor();
        this.$router.push({
            path:"/assetpage/",
          query: { name:"", saodata:e,id:"123",url:"/asm/Asset/Search?tabid=123&assetstatus=manage&operatetype=1&APIUrl=assetpage"}
        });      
        this.reload(); 
        },
        Ewmcode(){ 
        if(this.$route.path!="/assetpage/"){
            this.saoma(this.Adminste)
        }else{
            this.$emit('refreshbizlines')
        }
        },
        reload(){
        let setli=$(".foot ul li");
        switch(global.cliction){
            case 1:
            $(setli[0]).children("span").eq(0).removeClass("TopImg1").addClass("TopImg1t");
            $(setli[0]).children("span").eq(1).addClass("SizeColor");           
            break;
            case 2:
            $(setli[1]).children("span").eq(0).removeClass("TopImg2").addClass("TopImg2t");
            $(setli[1]).children("span").eq(1).addClass("SizeColor");           
            break;
            case 4:
            $(setli[3]).children("span").eq(0).removeClass("TopImg4").addClass("TopImg4t");
            $(setli[3]).children("span").eq(1).addClass("SizeColor");           
            break;
            case 5:
            $(setli[4]).children("span").eq(0).removeClass("TopImg5").addClass("TopImg5t");
            $(setli[4]).children("span").eq(1).addClass("SizeColor");           
            break;
        }
        }
    },
    mounted(){
         let jsURL=location.href.split('#')[0];
        this.reload();
    }
}



