let scan = null;

export default {
   name:'v-sao',
  data() {
    return {
      codeUrl: '',
    }
  },
  methods: {
    //
    startRecognize() {
      let that = this;
      if (!window.plus) return;
      var filter=[plus.barcode.CODE128,plus.barcode.EAN13,plus.barcode.QR]
      scan = new plus.barcode.Barcode('bcid',filter);
      scan.onmarked = onmarked;

      function onmarked(type, result, file) {
        switch (type) {
          case plus.barcode.QR:
            type = 'QR';
            break;
          case plus.barcode.EAN13:
            type = 'EAN13';
            break;
          // case plus.barcode.EAN8:
          //   type = 'EAN8';
          //   break;
          case plus.barcode.CODE128:
              type="CODE128";
              break;
          default:
            type = '' + type;
            break;
        }
        result = result.replace(/\n/g, '');
        that.codeUrl = result;
        that.cancelScan()
        that.closeScan();
        bus.$emit('saoshowload',false);
      bus.$emit('global.saodata',result);
                   
      }
    },
    //
    startScan() {
      if (!window.plus) return;
      scan.start();
    },
    //
    cancelScan() {
      if (!window.plus) return;
      scan.cancel();
    },
    //
    closeScan() {
      if (!window.plus) return;
      scan.close();
    },
    childFn(){
    	this.startRecognize();
    	this.startScan()
    },
    clickfanh(){
        this.cancelScan()
        this.closeScan();
        bus.$emit('saoshowload',false);
    }
  },
  mounted(){
  this.childFn();
}
}



